#aoffy {
  -webkit-animation: slideright 5s infinite;
          animation: slideright 5s infinite;
}

#love {
  -webkit-animation: showup 5s infinite;
          animation: showup 5s infinite;
}

#javascript {
  -webkit-animation: slideleft 5s infinite;
          animation: slideleft 5s infinite;

}

@-webkit-keyframes showup {
  0% {opacity: 0}
  50% {opacity: 0}
  70% {opacity: 1}
  90% {opacity: 1}
  100% {opacity: 0}
}

@keyframes showup {
  0% {opacity: 0}
  50% {opacity: 0}
  70% {opacity: 1}
  90% {opacity: 1}
  100% {opacity: 0}
}

@-webkit-keyframes slideright {
  0% { opacity: 0; margin-left: -50%; }
  20% { opacity: 0; margin-left: -50%; }
  35% { opacity: 1; margin-left: 0px; }
  90% { opacity: 1; margin-left: 0px; }
  100% { opacity: 0; margin-left: 0px; }
}

@keyframes slideright {
  0% { opacity: 0; margin-left: -50%; }
  20% { opacity: 0; margin-left: -50%; }
  35% { opacity: 1; margin-left: 0px; }
  90% { opacity: 1; margin-left: 0px; }
  100% { opacity: 0; margin-left: 0px; }
}

@-webkit-keyframes slideleft {
  0% { opacity: 0; margin-left: 50%; }
  20% { opacity: 0; margin-left: 50%; }
  35% { opacity: 1; margin-left: 0px; }
  90% { opacity: 1; margin-left: 0px; }
  100% { opacity: 0; margin-left: 0px; }
}

@keyframes slideleft {
  0% { opacity: 0; margin-left: 50%; }
  20% { opacity: 0; margin-left: 50%; }
  35% { opacity: 1; margin-left: 0px; }
  90% { opacity: 1; margin-left: 0px; }
  100% { opacity: 0; margin-left: 0px; }
}


